import React from "react";
import MapContainer from "../MapContainer";


const contacdata = [
    {
        title: 'LOCATION',
        description: '  14 20  5'
    },
    {
        title: 'EMAIL',
        description: 'webmaster@pitapat.net'
    },
    {
        title: 'CONTACT',
        description: 'TEL 070-7510-4328'
    }
]
const contacdataList = contacdata.map( (data, index) => <li key={index}>
        <b>{data.title}</b>
        <p>{data.description}</p> 
    </li>
);

const Contact = ({sections}) => {

    return (
        <div id={sections[3]} className="element" name="section4">
            <div className="cont_wrap">
                <div className="mapBox">
                    <MapContainer />
                </div>
                <div className="contactBox">
                    <div className="co-leftBox">
                        <h1>CONTACT US</h1>
                        <a href="mailto:webmaster@pitapat.net"></a>
                    </div>
                    <ul className="co-rightBox">
                        {contacdataList}
                    </ul>
                </div>
            </div>
        </div>
    );
};

export default Contact;
